@media (max-width: 1199px) {
  .about__container {
    padding: 126px 0 122px;
  }
}
@media (max-width: 767px) {
  .about__container {
    padding: 50px 0 55px;
  }
}
.about__container:before,
.about__container:after {
  content: '';
  position: absolute;
  z-index: 2;
  width: 274px;
  height: 10px;
  background: #000;
}
.about__container:before {
  top: 0;
  right: 50%;
  margin-right: 303px;
}
.about__container:after {
  bottom: 0;
  left: 50%;
  margin-left: 302px;
}
.about__title {
  position: relative;
  font-size: 48px;
  line-height: 1.25;
  font-family: 'Museo Sans Cyrl 900', sans-serif;
  margin-bottom: 64px;
  margin-left: -3px;
}
.about__title:before,
.about__title:after {
  content: '';
  position: absolute;
  background: #006dfe;
}
.about__title:before {
  width: 17px;
  height: 17px;
  top: -76px;
  left: 1px;
}
@media (max-width: 767px) {
  .about__title:before {
    display: none;
  }
}
.about__title:after {
  width: 42px;
  height: 8px;
  bottom: -17px;
  left: 1px;
}
.about__text {
  max-width: 770px;
  font-size: 16px;
  line-height: 1.875;
  margin-left: -3px;
  max-height: 210px;
  overflow: hidden;
  transition: all 0.2s ease-in;
}
.about__text--full {
  max-height: auto !important;
}
.about__button {
  margin-left: -2px;
  margin-top: 62px;
  font-size: 24px;
  width: 320px;
  opacity: 0;
  visibility: hidden;
  transition: all 0.1s ease-in;
}
.about__button--active {
  opacity: 1;
  visibility: visible;
}
.about__button svg {
  width: 24px;
  height: 24px;
}
.about__decor {
  position: absolute;
  width: 240px;
  height: 584px;
  border: 6px solid #006dfe;
  right: 97px;
  top: 229px;
}
@media (max-width: 1199px) {
  .about__decor {
    display: none;
  }
}
.about__decor:before {
  content: '';
  position: absolute;
  width: 13px;
  height: 193px;
  background: #000;
  right: 100%;
  bottom: 65px;
}
.about__image {
  position: absolute;
  top: 98px;
  left: 78px;
}
.about__image img {
  transition: all 0.2s ease-in;
  transform: translateX(30px);
}
.about__image img.lazy--visible {
  transform: none;
}
.about__image2 {
  position: absolute;
  right: 50%;
  bottom: -20px;
  margin-right: 85px;
  z-index: 2;
  pointer-events: none;
}
@media (max-width: 1199px) {
  .about__image2 {
    margin-right: -200px;
    bottom: -60px;
  }
}
@media (max-width: 767px) {
  .about__image2 {
    display: none;
  }
}
